import {  
    mapState,
	mapMutations  
} from 'vuex';  
export default {
	data() {
		return {
			titleNViewBackground: '#fff',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [], //banner部分
			newList: [], //最新上架
			randomList: [],//随机推荐
			categoryData:[],//分类数据
			searchName:'',//搜索名称
			isWeiXinLogin:true,
			token:''
		};
	},
	 computed: {
		...mapState(['hasLogin','userInfo'])
	},
	onLoad() {
		this.loadData();
		//获取分类信息
		this.getCategoryInformation();
		//处理授权
		this.doAuthorize();
	},
	methods: {
		 ...mapMutations(["login"]),
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			let self = this;
			self.$axios(self.$baseUrl.homePage,{})
			.then(res =>{
				if(res.data.code == 1){
					let carouselList = res.data.data.bannerlist; //轮播图列表
					self.swiperLength = carouselList.length;
					self.carouselList = carouselList;
					self.newList = res.data.data.NewList;//最新上架列表
					self.randomList = res.data.data.Randomlist; //随机推荐列表
				}
				
			},(error) =>{
				console.log(error)
			})
		
		},
		//获取分类信息
		getCategoryInformation(){
			let self = this;
			self.$axios(self.$baseUrl.getCategoryData,{})
			.then(res =>{
				if(res.data.code == 1){
					self.categoryData = res.data.data.categorydata;
				}
				
			},(error) =>{
				console.log(error)
			})
		},
		
		onSearch: function () {
			let name = this.searchName;
			// 搜索页 
			uni.navigateTo({
				url: `/pages/list/list?name=${name}`
			})
		},
			
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//banner 详情页面
		navToBannerDetailPage(item) {
			let id = item.id;
			uni.navigateTo({
				url: `/pages/bannerdetail/bannerdetail?id=${id}`
			})
		},
		//详情页
		navToDetailPage(item) {
			
			let id = item.goods_id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		//分类列表页面
		navToCategoryList(item){
			
			let id = item.id;
			let name = '';
			uni.navigateTo({
				url: `/pages/list/list?id=${id}&name=${name}`
			})
		},
		doAuthorize(){
			let self=this;
			setTimeout(function () {
			
			 if(!self.hasLogin){ 
			 	self.doRealAuthorize();
			 }
			 //self.setFristUserInfo(self.userInfo);
			}, 300) //延迟时间 这里是1秒
		},
		onGetUserInfo(e){
			if(e.mp.detail.userInfo){
				this.$data.isWeiXinLogin=true;
				uni.showTabBar();
				this.doRealAuthorize();
				this.setFristUserInfo(e.mp.detail.userInfo);
			}
		},
		setFristUserInfo(userInfo){
			if(userInfo.avatarUrl!==undefined
					&&userInfo.nickName!==undefined
					&&userInfo.gender!==undefined){
				this.userInfo.portrait=userInfo.avatarUrl;
				this.userInfo.nickname=userInfo.nickName;
				this.userInfo.gender=userInfo.gender;
			}
			console.log("this.userInfo.portrait============>"+this.userInfo.portrait);
			console.log("this.userInfo.nickname============>"+this.userInfo.nickname);
			console.log("this.userInfo.gender============>"+this.userInfo.gender);
			let self=this;
			self.$axios(self.$baseUrl.setFirstUserInfo,
			{avatarUrl:this.userInfo.portrait,
			 nickname:this.userInfo.nickname,
			 gender:this.userInfo.gender
			}).then(res =>{
				
			},(error) =>{
				
			})	
		},
		doRealAuthorize(){
			//显示的时候先隐藏tabbar
			var that=this;
			uni.hideTabBar();
			uni.getSetting({
			success: (res) => {
			console.log(res)
			console.log('是否授权', res.authSetting['scope.userInfo']);
			this.$data.isWeiXinLogin=(res.authSetting['scope.userInfo']!== false)&&(res.authSetting['scope.userInfo']!== undefined);
			if(this.$data.isWeiXinLogin===true){
				//如果授权了就显示 tabbar
				uni.showTabBar();
				uni.getUserInfo({
					success(res) {
					console.log(res)
					console.log(res.encryptedData)
					that.userInfo.portrait=res.userInfo.avatarUrl;
					that.userInfo.nickname=res.userInfo.nickName;
					//that.setFristUserInfo(res.userInfo);
					//微信登录获取code授权码
					uni.login({
						success(res) {
						//调用登录接口
						console.log(res.code);
						let self = that;
						uni.showLoading({
							title:'正在加载...'
						})
						self.$axios(self.$baseUrl.login,{code:res.code})
						.then(res =>{
							uni.hideLoading();
							var loginToken=res.data.data.userInfo.token;
							console.log(res);
							console.log(loginToken);
							var avatar=res.data.data.userInfo.avatar;
							var nickname=res.data.data.userInfo.nickname;
							var gender=res.data.data.userInfo.gender;
							var birthday=res.data.data.userInfo.birthday;
							var qianming=res.data.data.userInfo.qianming;
							console.log('-------------------avater------------------'+avatar);
							self.userInfo.portrait=(avatar==''||avatar==undefined)?self.userInfo.portrait:avatar;
							self.userInfo.nickname=(nickname==''||nickname==undefined)?self.userInfo.nickname:nickname;
							self.userInfo.gender=(gender==''||gender==undefined)?self.userInfo.gender:gender;
							self.userInfo.birthday=(birthday==''||birthday==undefined)?self.userInfo.birthday:birthday;
							self.userInfo.qianming=(qianming==''||qianming==undefined)?self.userInfo.qianming:qianming;
							//存下本地的数据
							self.userInfo.token=loginToken;
							self.login(self.userInfo);
							setTimeout(function(){
								self.setFristUserInfo(self.userInfo);
							},500);
						},(error) =>{
							uni.hideLoading();
							console.log(error)
						})	
						
						}
					})
					
					}
				})
				}
				}		
			})      
		}
	},























}
